import React, { useState, useRef, useEffect } from "react";
import wrapper from "../../commons/WebComponentWrapper";

const dispatchedEvents = ["ABOUT#clicked"];

/*
    Example: <srs-iframe src="#" height="500px" width="800px" allow_scripts="false" allow_forms="false" allow_same_origin="true"/>
*/
const SrsIframe = function (props, ref) {
    let sandbox = "";

    if(props.allow_scripts) {
        sandbox += " allow-scripts"
    }
    if(props.allow_forms) {
        sandbox += " allow-forms";
    }
    if(props.allow_same_origin) {
        sandbox += " allow-same-origin";
    }
    let extraProps = {};
    if(props.width) {
        extraProps.width = props.width;
    }
    if(props.height) {
        extraProps.height = props.height;
    }

    return (
        <iframe src={props.src} sandbox={sandbox} {...extraProps}/>
    );
}

const styles = `
    
`
const props = [{
    name: "src",
    defaultValue: "#",
    type: "string"
}, {
    name: "allow_scripts",
    defaultValue: false,
    type: "boolean"
}, {
    name: "allow_forms",
    defaultValue: false,
    type: "boolean"
}, {
    name: "allow_same_origin",
    defaultValue: true,
    type: "boolean"
}, {
    name: "width",
    defaultValue: "100%",
    type: "string"
}, {
    name: "height",
    defaultValue: "100%",
    type: "string"
}]

const slots = [];

const WrappedComponent = wrapper(React.forwardRef(SrsIframe), props);
export { WrappedComponent, styles, props, dispatchedEvents, slots };